import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import { Button, withStyles, TextField } from "@material-ui/core";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class Calculator extends Component {
  state = {
    openCalc: false,
    openCur: false,
    openRate: false,
    currency: {
      currencyName: "",
      value: 0.0,
      isInTable: false
    },
    rateName: "",
    rateValue: 0.0,
    manualRate: 0.0,
    amount: 0.0
  };

  open = label => {
    switch (label) {
      case "calculator":
        this.setState({ openCalc: !this.state.openCalc });
        break;
      case "currency":
        this.setState({ openCur: !this.state.openCur });
        break;
      case "rate":
        this.setState({ openRate: !this.state.openRate });
        break;
      default:
        break;
    }
  };

  handleCurrencyChoise = (currencyName, currencyValue, checkIfInTable) => {
    let checkRate = false;
    if (!checkIfInTable) {
      this.props.user.currenciesRates.forEach(rate => {
        if (rate.currencyName === currencyName) {
          checkRate = true;
        }
      });

      this.open("currency");
    } else {
      checkRate = true;
    }

    this.setState({
      amount: 0.0,
      manualRate: 0.0,
      currency: {
        ...this.state.currency,
        currencyName: currencyName,
        value: currencyValue,
        isInTable: checkRate
      }
    });
  };

  handleRateChoise = rateAction => {
    const { currency } = this.state;

    this.props.user.currenciesRates.forEach(rate => {
      if (rate.currencyName === currency.currencyName) {
        this.setState({
          rateName: rateAction,
          rateValue: rate[rateAction]
        });
      }
    });

    this.open("rate");
    return;
  };

  handleChange = ({ target: input }) => {
    this.setState({
      [input.name]: input.value
    });
  };

  render() {
    const { classes, currencies, user } = this.props;
    const {
      openCalc,
      openCur,
      openRate,
      currency,
      amount,
      manualRate,
      rateName,
      rateValue
    } = this.state;
    return (
      <Fragment>
        <Button onClick={() => this.open("calculator")}>Calculator</Button>
        {openCalc && (
          <Fragment>
            <Button
              aria-controls="simple-menu"
              aria-haspopup="true"
              onClick={() => this.open("currency")}
            >
              {currency.currencyName || "currency"}
            </Button>
            <Menu
              id="simple-menu"
              open={openCur}
              onClose={() => this.open("currency")}
            >
              {Object.keys(currencies).map((currency, i) => (
                <MenuItem
                  key={i}
                  onClick={() =>
                    this.handleCurrencyChoise(
                      currency,
                      currencies[currency],
                      false
                    )
                  }
                >
                  {currency}
                </MenuItem>
              ))}
            </Menu>

            {user.currenciesRates.map(
              rate =>
                rate.isInTable && (
                  <Button
                    key={rate._id}
                    onClick={() =>
                      this.handleCurrencyChoise(
                        rate.currencyName,
                        currencies[rate.currencyName],
                        true
                      )
                    }
                  >
                    {rate.currencyName}
                  </Button>
                )
            )}

            {currency.currencyName && (
              <Fragment>
                <div>{currency.value || "0"}</div>

                <TextField
                  className={classes.textField}
                  autoFocus
                  name="manualRate"
                  label="manual rate"
                  type="manualRate"
                  value={manualRate}
                  onChange={this.handleChange}
                  fullWidth
                  required
                />
                <TextField
                  className={classes.textField}
                  autoFocus
                  name="amount"
                  label="amount"
                  type="amount"
                  value={amount}
                  onChange={this.handleChange}
                  fullWidth
                  required
                />
              </Fragment>
            )}

            {currency.isInTable && (
              <Fragment>
                <Button
                  aria-controls="simple-menu-2"
                  aria-haspopup="true"
                  onClick={() => this.open("rate")}
                >
                  {rateName || "action"}
                </Button>
                <Menu
                  id="simple-menu-2"
                  open={openRate}
                  onClose={() => this.open("rate")}
                >
                  <MenuItem
                    onClick={() => this.handleRateChoise("buyCashRate")}
                  >
                    buy cash
                  </MenuItem>
                  <MenuItem
                    onClick={() => this.handleRateChoise("sellCashRate")}
                  >
                    sell cash
                  </MenuItem>
                  <MenuItem
                    onClick={() => this.handleRateChoise("buyTransferRate")}
                  >
                    buy by transfer
                  </MenuItem>
                  <MenuItem
                    onClick={() => this.handleRateChoise("sellTransferRate")}
                  >
                    sell by transfer
                  </MenuItem>
                </Menu>
              </Fragment>
            )}
            <div>
              {(Number(currency.value) + rateValue + Number(manualRate)) *
                amount}
            </div>
          </Fragment>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    user: state.userReducer.user,
    currencies: state.getCurrenciesReducer.currencies
  };
};

export default connect(mapStateToProps, null)(withStyles(styles)(Calculator));
