import React, { Fragment, Component } from "react";
import { withStyles } from "@material-ui/core";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class NotFound extends Component {
  render() {
    // const classes = this.props;
    return (
      <Fragment>
        <div>404 not found</div>
      </Fragment>
    );
  }
}

export default withStyles(styles)(NotFound);
