import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import { Button, withStyles } from "@material-ui/core";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import { currenciesUpdateRequest } from "../../../Redux/Actions/MarketDataActions/Currencies/getCurrenciesActions";
import { updateRateRequest } from "../../../Redux/Actions/MarketDataActions/Rates/updateRateActions";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class TableManagement extends Component {
  state = {
    open: false
  };

  componentDidMount() {
    if (this.props.LoggedIn) {
    } else {
      this.props.history.push("/loginPage");
    }
  }

  handleToggle = () => {
    this.setState({ open: !this.state.open });
  };

  handleChoise = chosenRate => {
    chosenRate.isInTable = !chosenRate.isInTable;
    this.props.updateRateRequest(chosenRate);
    this.handleToggle();
  };

  render() {
    const { user, loadedCurrencies, currencies } = this.props;
    return (
      <Fragment>
        <ToastContainer />
        {loadedCurrencies && (
          <Fragment>
            <Button
              aria-controls="simple-menu"
              aria-haspopup="true"
              onClick={() => this.handleToggle()}
            >
              add/delete
            </Button>
            <Menu
              id="simple-menu"
              open={this.state.open}
              onClose={() => this.handleToggle()}
            >
              {user.currenciesRates.map(rate => (
                <MenuItem
                  key={rate._id}
                  onClick={() => this.handleChoise(rate)}
                >
                  {rate.currencyName}
                </MenuItem>
              ))}
            </Menu>
            <table>
              <thead>
                <tr>
                  <th rowSpan="2">flag</th>
                  <th rowSpan="2">currency</th>
                  <th rowSpan="2">value</th>
                  <th colSpan="2" scope="colgroup">
                    cash
                  </th>
                  <th colSpan="2" scope="colgroup">
                    transfer
                  </th>
                </tr>
                <tr>
                  <th>buy </th>
                  <th>sell </th>
                  <th>buy </th>
                  <th>sell </th>
                </tr>
              </thead>
              <tbody>
                {user.currenciesRates.map(
                  rate =>
                    rate.isInTable && (
                      <tr className="col col-lg-4 grid-wrapper" key={rate._id}>
                        <td> </td>
                        <td> {rate.currencyName}</td>
                        <td> {currencies[rate.currencyName]}</td>
                        <td>
                          {" "}
                          {rate.buyCashRate + currencies[rate.currencyName]}
                        </td>
                        <td>
                          {" "}
                          {rate.sellCashRate + currencies[rate.currencyName]}
                        </td>
                        <td>
                          {" "}
                          {rate.buyTransferRate + currencies[rate.currencyName]}
                        </td>
                        <td>
                          {" "}
                          {rate.sellTransferRate +
                            currencies[rate.currencyName]}
                        </td>
                      </tr>
                    )
                )}
              </tbody>
            </table>
          </Fragment>
        )}
      </Fragment>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  updateRateRequest: rate => dispatch(updateRateRequest(rate)),
  currenciesUpdateRequest: currencies =>
    dispatch(currenciesUpdateRequest(currencies))
});

const mapStateToProps = state => {
  return {
    loadedCurrencies: state.getCurrenciesReducer.loadedCurrencies,
    currencies: state.getCurrenciesReducer.currencies,
    LoggedIn: state.userReducer.LoggedIn,
    user: state.userReducer.user
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(TableManagement));
