import React, { Fragment, Component } from "react";
import PropTypes from "prop-types";
import { withStyles, Button, TextField } from "@material-ui/core";
import { addRateRequest } from "../../../Redux/Actions/MarketDataActions/Rates/addRateActions";
import { connect } from "react-redux";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import { toast } from "react-toastify";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const styles = {
  root: {
    margin: "20px"
  }
};

class AddRatePage extends Component {
  state = {
    open: false,
    rate: {
      rateName: "",
      rateValue: 0,
      buyCashRate: "",
      sellCashRate: "",
      buyTransferRate: "",
      sellTransferRate: "",
      isInTable: false
    }
  };

  componentDidMount() {
    if (!this.props.LoggedIn) {
      this.props.history.push("/loginPage");
    }
  }

  componentDidUpdate() {
    if (this.props.addRateSuccess) {
      this.props.history.push("/rateManagement");
    }
  }

  handleChange = ({ target: input }) => {
    this.setState({
      rate: {
        ...this.state.rate,
        [input.name]: input.value
      }
    });
  };

  renderInput(name) {
    const classes = this.props;
    return (
      <TextField
        className={classes.textField}
        autoFocus
        name={name}
        label={name}
        type="text"
        autoComplete="off"
        value={this.state.rate[name]}
        onChange={this.handleChange}
        fullWidth
        required
      />
    );
  }

  submit = e => {
    if (this.state.rate.rateName === "") {
      toast("please select currency type");
      e.preventDefault();
    } else {
      this.props.addRateRequest(this.state.rate);
      e.preventDefault();
    }
  };

  handleToggle = () => {
    this.setState({ open: !this.state.open });
  };

  handleChoise = (name, value) => {
    this.setState({
      rate: { ...this.state.rate, rateName: name, rateValue: value },
      open: !this.state.open
    });
  };

  render() {
    const { classes, currencies, LoggedIn } = this.props;
    return (
      <Fragment>
        {LoggedIn && (
          <Fragment>
            <ToastContainer />
            <form
              className={classes.container}
              onSubmit={this.submit.bind(this)}
            >
              <div>
                currency :
                <Button
                  aria-controls="simple-menu"
                  aria-haspopup="true"
                  onClick={this.handleToggle}
                >
                  {this.state.rate.rateName || "select currency"}
                </Button>
                <Menu
                  id="simple-menu"
                  open={this.state.open}
                  onClose={this.handleToggle}
                >
                  {Object.keys(currencies).map((currency, i) => (
                    <MenuItem
                      key={i}
                      onClick={() =>
                        this.handleChoise(currency, currencies[currency])
                      }
                    >
                      {currency}
                    </MenuItem>
                  ))}
                </Menu>
              </div>
              <div>value: {this.state.rate.rateValue}</div>
              {this.renderInput("buyCashRate")}
              {this.renderInput("sellCashRate")}
              {this.renderInput("buyTransferRate")}
              {this.renderInput("sellTransferRate")}

              <Button type="submit" color="primary" variant="outlined">
                Add
              </Button>
              <Button
                type="button"
                color="primary"
                variant="outlined"
                onClick={() => {
                  this.props.history.push("/rateManagement");
                }}
              >
                Cancel
              </Button>
            </form>
          </Fragment>
        )}
      </Fragment>
    );
  }
}

AddRatePage.propTypes = {
  classes: PropTypes.object.isRequired
};

const mapDispatchToProps = dispatch => ({
  addRateRequest: rate => dispatch(addRateRequest(rate))
});

const mapStateToProps = state => {
  return {
    LoggedIn: state.userReducer.LoggedIn,
    currencies: state.getCurrenciesReducer.currencies,
    addRateSuccess: state.addRateReducer.addRateSuccess
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(AddRatePage));
