import {
  USER_LOGIN_SUCCESS,
  USER_LOGIN_FAIL,
  USER_LOGIN_REQUEST,
  USER_LOGOUT_REQUEST,
  USER_LOGOUT_SUCCESS,
  USER_LOAD_FAIL,
  USER_LOAD_REQUEST,
  USER_LOAD_SUCCESS,
  CHECK_LOGGED_IN,
  CHECK_LOGGED_IN_SUCCESS,
  CHECK_LOGGED_IN_FAIL
} from "../../Actions/UserActions/userActions";

const initialState = {
  user: {},
  loadLogin: false,
  loadedUser: false,
  LoggedIn: false,
  LoggedInRequest: false,
  initialLogin: false
};

export default function(state = initialState, action) {
  switch (action.type) {
    case CHECK_LOGGED_IN:
      return {
        ...state,
        loadLogin: false,
        LoggedInRequest: false
      };
    case CHECK_LOGGED_IN_SUCCESS:
      if (action.payload === true) {
        return {
          ...state,
          loadLogin: false,
          LoggedInRequest: true,
          LoggedIn: true
        };
      } else {
        return {
          ...state,
          loadLogin: true,
          LoggedInRequest: true,
          LoggedIn: false
        };
      }
    case CHECK_LOGGED_IN_FAIL:
      return {
        ...state,
        loadLogin: false,
        LoggedInRequest: true,
        LoggedIn: false
      };
    case USER_LOGIN_REQUEST:
      return {
        loadLogin: true
      };
    case USER_LOGIN_SUCCESS:
      return {
        ...state,
        loadLogin: false,
        LoggedIn: true
      };
    case USER_LOGIN_FAIL:
      return {
        ...state,
        loadLogin: true,
        LoggedIn: false
      };
    case USER_LOGOUT_REQUEST:
      return {};
    case USER_LOGOUT_SUCCESS:
      return {
        ...state,
        loadLogin: true,
        LoggedIn: false,
        loadedUser: false,
        user: {}
      };
    case USER_LOAD_REQUEST:
      return {
        ...state,
        LoggedIn: true,
        loadedUser: false
      };
    case USER_LOAD_SUCCESS:
      return {
        ...state,
        LoggedIn: true,
        loadedUser: true,
        user: { ...action.payload }
      };
    case USER_LOAD_FAIL:
      return {
        ...state,
        loadedUser: false,
        LoggedIn: false
      };
    default:
      return state;
  }
}

