import { toast } from "react-toastify";
import {
  UPDATE_RATE_SUCCESS,
  UPDATE_RATE_FAIL,
  UPDATE_RATE_REQUEST,
  LOAD_UPDATE_RATE_FORM
} from "../../../Actions/MarketDataActions/Rates/updateRateActions";

const initialState = {
  desiredRate: {},
  loadedUpdateRate: false,
  updateRateSuccess: false
};

export default function(state = initialState, action) {
  switch (action.type) {
    case LOAD_UPDATE_RATE_FORM:
      return {
        ...state,
        loadedUpdateRate: true,
        updateRateSuccess: false,
        desiredRate: action.payload.rate
      };
    case UPDATE_RATE_REQUEST:
      return { ...state, updateRateSuccess: false, loadedUpdateRate: false };
    case UPDATE_RATE_SUCCESS:
      return { ...state, updateRateSuccess: true, loadedUpdateRate: false };
    case UPDATE_RATE_FAIL:
      toast("failed to update rate");
      return { ...state, updateRateSuccess: false, loadedUpdateRate: false };
    default:
      return state;
  }
}
