import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import { Button, withStyles } from "@material-ui/core";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import MenuItem from "@material-ui/core/MenuItem";
import { Link } from "react-router-dom";
import { userLoadRequest } from "../../../Redux/Actions/UserActions/userActions";
import { loadUpdateRateForm } from "../../../Redux/Actions/MarketDataActions/Rates/updateRateActions";
import { loadAddRateForm } from "../../../Redux/Actions/MarketDataActions/Rates/addRateActions";
import {
  deleteRateRequest,
  setDeleteRateProps
} from "../../../Redux/Actions/MarketDataActions/Rates/deleteRateActions";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class RateManagement extends Component {
  componentDidMount() {
    this.props.userLoadRequest();
  }

  componentDidUpdate() {
    if (!this.props.LoggedIn) {
      this.props.history.push("/loginPage");
    }

    if (this.props.deleteRateSuccess) {
      this.props.userLoadRequest();
      this.props.setDeleteRateProps();
    }
  }

  handleAddRate = () => {
    this.props.loadAddRateForm();
    this.props.history.push("/addRatePage");
  };

  handleUpdateRate = rate => {
    this.props.loadUpdateRateForm(rate);
    this.props.history.push("/updateRate");
  };

  handleRateDelete = name => {
    this.props.deleteRateRequest(name);
  };

  render() {
    const { loadedUser, user, currencies } = this.props;
    return (
      <Fragment>
        <ToastContainer />
        {loadedUser && (
          <Fragment>
            <MenuItem component={Link} to={"/userPage"}>
              return to user page
            </MenuItem>
            <Button onClick={this.handleAddRate}>add rate</Button>
            <table>
              <thead>
                <tr>
                  <th rowSpan="2">currency</th>
                  <th rowSpan="2">value</th>
                  <th colSpan="2" scope="colgroup">
                    cash
                  </th>
                  <th colSpan="2" scope="colgroup">
                    transfer
                  </th>
                </tr>
                <tr>
                  <th>buy </th>
                  <th>sell </th>
                  <th>buy </th>
                  <th>sell </th>
                </tr>
              </thead>
              <tbody>
                {user.currenciesRates.map(rate => (
                  <tr className="col col-lg-4 grid-wrapper" key={rate._id}>
                    <td> {rate.currencyName}</td>
                    <td> {currencies[rate.currencyName]}</td>
                    <td> {rate.buyCashRate}</td>
                    <td> {rate.sellCashRate}</td>
                    <td> {rate.buyTransferRate}</td>
                    <td> {rate.sellTransferRate}</td>
                    <td>
                      <Button
                        onClick={() => this.handleRateDelete(rate.currencyName)}
                      >
                        delete
                      </Button>
                    </td>
                    <td>
                      <Button onClick={() => this.handleUpdateRate(rate)}>
                        update
                      </Button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </Fragment>
        )}
      </Fragment>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  userLoadRequest: () => dispatch(userLoadRequest()),
  loadUpdateRateForm: rate => dispatch(loadUpdateRateForm(rate)),
  loadAddRateForm: () => dispatch(loadAddRateForm()),
  deleteRateRequest: id => dispatch(deleteRateRequest(id)),
  setDeleteRateProps: () => dispatch(setDeleteRateProps())
});

const mapStateToProps = state => {
  return {
    user: state.userReducer.user,
    loadedUser: state.userReducer.loadedUser,
    LoggedIn: state.userReducer.LoggedIn,
    deleteRateSuccess: state.deleteRateReducer.deleteRateSuccess,
    currencies: state.getCurrenciesReducer.currencies
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(RateManagement));
