import {
  GET_USERS_SUCCESS,
  GET_USERS_FAIL,
  GET_USERS_REQUEST
} from "../../Actions/AdminActions/getUsersActions";

const initialState = { checkIfAdmin: false, isAdmin: false, users: [] };

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_USERS_REQUEST:
      return { ...state, users: [], checkIfAdmin: false, isAdmin: false };
    case GET_USERS_SUCCESS:
      return {
        ...state,
        users: [...action.payload],
        checkIfAdmin: true,
        isAdmin: true
      };
    case GET_USERS_FAIL:
      return { ...state, users: [], checkIfAdmin: false, isAdmin: false };
    default:
      return state;
  }
}
