import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import { Button, withStyles } from "@material-ui/core";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import MenuItem from "@material-ui/core/MenuItem";
import {
  logoutRequest,
  userLoadRequest
} from "../../Redux/Actions/UserActions/userActions";
import TableManagement from "../MarketData/Table/tableManagement";
import Calculator from "../MarketData/Table/calculator";
import { loadUpdateForm } from "../../Redux/Actions/AdminActions/updateUserActions";
import io from "socket.io-client";
import { currenciesUpdateRequest } from "../../Redux/Actions/MarketDataActions/Currencies/getCurrenciesActions";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class UserPage extends Component {
  componentDidMount() {
    this.props.userLoadRequest();
    this.socket = io("https://www.moneyexchangeco.com/socket.io");
    //http://localhost:3000/socket.io
    //https://www.moneyexchangeco.com/socket.io
    this.socket.on("message", currencies => {
      currenciesUpdateRequest(currencies);
    });
    this.loadData();
  }

  componentDidUpdate() {
    if (!this.props.LoggedIn) {
      this.props.history.push("/loginPage");
    }
  }

  componentWillUnmount() {
    this.socket.close();
  }

  loadData() {
    this.socket.on("message", currencies => {
      this.props.currenciesUpdateRequest(currencies);
    });
  }

  handleLogout = e => {
    this.props.logoutRequest();
    this.props.history.push("/loginPage");
    e.preventDefault();
  };

  handleAdmin = () => {
    this.props.history.push("/adminPage");
  };

  handleUpdate = user => {
    this.props.loadUpdateForm(user);
    this.props.history.push("/updatePage");
  };

  handleRates = () => {
    this.props.history.push("/rateManagement");
  };

  render() {
    const { user, loadedUser } = this.props;
    return (
      <Fragment>
        <ToastContainer />
        {loadedUser && (
          <Fragment>
            <div>{user.name}</div>
            <div>{user.email}</div>
            <div>store name: {user.storeName}</div>
            <div>address: {user.address}</div>
            <div>licence num: {user.licenceNumber}</div>
            <Fragment>
              <Button onClick={() => this.handleUpdate(user)}>
                user settings
              </Button>
              <Button onClick={() => this.handleRates()}>rate settings</Button>
            </Fragment>
            {user.isAdmin && (
              <MenuItem onClick={() => this.handleAdmin()}>
                Admin managment
              </MenuItem>
            )}
            <TableManagement />
            <Calculator />
            <Button onClick={e => this.handleLogout(e)}>logout</Button>
          </Fragment>
        )}
      </Fragment>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  logoutRequest: () => dispatch(logoutRequest()),
  userLoadRequest: () => dispatch(userLoadRequest()),
  loadUpdateForm: user => dispatch(loadUpdateForm(user)),
  currenciesUpdateRequest: currencies =>
    dispatch(currenciesUpdateRequest(currencies))
});

const mapStateToProps = state => {
  return {
    loadedUser: state.userReducer.loadedUser,
    LoggedIn: state.userReducer.LoggedIn,
    user: state.userReducer.user
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(UserPage));
